@charset "UTF-8";
body {
  width: 100%;
  height: 100%; }

input {
  background: none;
  outline: none;
  border: 0px; }

input[type="text"]:focus,
input[type="password"]:focus {
  outline: none; }

input, button, select, textarea {
  outline: none; }

textarea {
  resize: none; }

input, button, select, textarea {
  outline: none;
  border: none; }

textarea {
  resize: none; }

textarea:focus {
  outline: none;
  border: none; }

input[type="checkbox"] + label::before {
  content: "\a0";
  /*不换行空格*/
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 2px;
  text-align: center;
  line-height: 20px;
  border: 1px solid #ddd; }

/*input 选中后的样式 */
input[type="checkbox"]:checked + label::before {
  border: none;
  transform: rotate(90deg);
  background: #D8D8D8; }

input[type=checkbox] + span {
  /* your style goes here */
  display: inline-block;
  height: 18px;
  width: 18px;
  border-radius: 4px;
  border: 1px solid #C6C6C6; }

/* active style goes here */
input[type=checkbox]:checked + span {
  background: #fff; }

